	module.exports = {"data":{"markdownRemark":{"html":"<h1>v5 Release!</h1>\n<p><em>Lots of big things going on at A Race athlete and version 5 is one of the best updates yet!</em></p>\n<h2>TL;DR - I’m drinking espresso today!</h2>\n<ul>\n<li>Activity Search is out of beta (Club Member only feature*)</li>\n<li>Weekly Stats loads a million times faster** (Club Member only feature*)</li>\n<li>New Athlete on-boarding is more intuitive and Club Members get more feedback</li>\n<li>Printing is a thing</li>\n<li>UI migrated to Material-ui v1</li>\n<li>More stuff coming</li>\n</ul>\n<h2>Takin’ it Slow - I’ve got my latte and a little time read.</h2>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; margin: 0.4em auto; max-width: 412px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 18.181818181818183%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAECAYAAACOXx+WAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAgUlEQVQY06WPTQrDIBSEvf+FSk/RivhfVES6cFGhIYs01SkR2kVwUcjAW8y84YMhrVbU94pNrbV+/2rUJdPN4n4+oS4Lvm/vPYwxEEKAUgrnHKSUYIxBKdWznPMQStZ5xuN6wWt6/sKUEqy1CCF0wAaPMYJzDq1196WUMXA/4ejkD+PWNKaCqB7mAAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Activity Search Menu\"\n        title=\"\"\n        src=\"/blog/static/menu-activity-search-0aa859ae1d31344fc33226418b49ac2b-31818.png\"\n        srcset=\"/blog/static/menu-activity-search-0aa859ae1d31344fc33226418b49ac2b-b3e18.png 103w,\n/blog/static/menu-activity-search-0aa859ae1d31344fc33226418b49ac2b-571c4.png 206w,\n/blog/static/menu-activity-search-0aa859ae1d31344fc33226418b49ac2b-31818.png 412w,\n/blog/static/menu-activity-search-0aa859ae1d31344fc33226418b49ac2b-1be92.png 618w,\n/blog/static/menu-activity-search-0aa859ae1d31344fc33226418b49ac2b-05b5b.png 824w,\n/blog/static/menu-activity-search-0aa859ae1d31344fc33226418b49ac2b-58052.png 880w\"\n        sizes=\"(max-width: 412px) 100vw, 412px\"\n      />\n    </span>\n  </span>\n  </p>\n<h3>Activity Search</h3>\n<p>Have you ever wanted to find a ride on Strava and even though you know you have the name right, it doesn’t show up? Or maybe you want to find all the rides you did on vacation last summer on that island in the Mediterranean? Activity Search lets you search by both text and location and filter based on dates, mileage, elevation, and a whole host of other data. See more (<a href=\"/blog/activity-search\">here</a>).</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; margin: 0.4em auto; max-width: 412px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 18.181818181818183%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAECAYAAACOXx+WAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAdklEQVQY06VPSwqFMBDrzQVPpWfoh4LQz66btjvBQldiIw5S3uIJgoEhQyYTCMMPWms0b/DkY5tzSPOEvdYueu8hpYQxBpxzpJRoV0oR19v7L5StSmIZB2zOdjHnDK01PV8cQkCMEUIIWGtRSnkOpJrH0Y9fK59aUDPLTIz8IgAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Weekly Stats Menu\"\n        title=\"\"\n        src=\"/blog/static/menu-weekly-stats-67937420be460805be3582a1633e802c-31818.png\"\n        srcset=\"/blog/static/menu-weekly-stats-67937420be460805be3582a1633e802c-b3e18.png 103w,\n/blog/static/menu-weekly-stats-67937420be460805be3582a1633e802c-571c4.png 206w,\n/blog/static/menu-weekly-stats-67937420be460805be3582a1633e802c-31818.png 412w,\n/blog/static/menu-weekly-stats-67937420be460805be3582a1633e802c-1be92.png 618w,\n/blog/static/menu-weekly-stats-67937420be460805be3582a1633e802c-05b5b.png 824w,\n/blog/static/menu-weekly-stats-67937420be460805be3582a1633e802c-58052.png 880w\"\n        sizes=\"(max-width: 412px) 100vw, 412px\"\n      />\n    </span>\n  </span>\n  </p>\n<h3>Weekly Stats load times for Club Members</h3>\n<p>What is really going on here is that Strava now notifies A Race athlete when your ride is ready and we go grab it and crunch the numbers before you have time to finish your recovery drink. When you finally do get around to checking out your stats, the data is ready and waiting.</p>\n<h3>New Athlete on-boarding is more intuitive and Club Members get more feedback</h3>\n<p>New users will now see thirty activities on first login. Club Members will see a status bar on the weekly stats page any time they have pending Activities waiting to be processed.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; margin: 0.4em auto; max-width: 412px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 18.181818181818183%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAECAYAAACOXx+WAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAc0lEQVQY06WPQQqAIBREvXbQfaK71CKRFHRRuLQIoYVJ/ikKWkRB0NsMfJjHfJZiRBgGUEo4IMIT9HK/w8a6QpNnmHhzCbXWMMYcKYSA9/6zlM19h74s4GV7lval1lpIKaGUAucczrnvQkor1iVcBfr58gbnCTblFlwb8gAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Events Menu\"\n        title=\"\"\n        src=\"/blog/static/menu-events-e1a59ff3fcf34125089da440f236514a-31818.png\"\n        srcset=\"/blog/static/menu-events-e1a59ff3fcf34125089da440f236514a-b3e18.png 103w,\n/blog/static/menu-events-e1a59ff3fcf34125089da440f236514a-571c4.png 206w,\n/blog/static/menu-events-e1a59ff3fcf34125089da440f236514a-31818.png 412w,\n/blog/static/menu-events-e1a59ff3fcf34125089da440f236514a-1be92.png 618w,\n/blog/static/menu-events-e1a59ff3fcf34125089da440f236514a-05b5b.png 824w,\n/blog/static/menu-events-e1a59ff3fcf34125089da440f236514a-58052.png 880w\"\n        sizes=\"(max-width: 412px) 100vw, 412px\"\n      />\n    </span>\n  </span>\n  </p>\n<h3>Printing is a thing</h3>\n<p>Many of the pages have been designed with printing in mind. Of special note are the Events, Power at Altitude, and Activity Search pages. It was particularly important to make Events something that could easily be posted to the fridge.</p>\n<h3>UI migrated to Material-ui v1</h3>\n<p>This is more geeky than bike, but the migration to v5 saw hundreds of files and a whole lot of code changed to make the overall user experience better.</p>\n<h3>More Stuff Coming</h3>\n<p>My goal is to under promise and over deliver, so I don’t want to give too much away. But, look for Routes Search to be coming soon, as well as more graphs and charts for activities.</p>\n<p>*Club Member only feature\nThese features are only available to A Race athlete Strava Club members because we don’t store or auto retrieve Activities for non club members due to storage limitations. Joining is free and only takes a second.</p>\n<p>**Ok, maybe not a million times, but it is quite a bit faster.</p>","frontmatter":{"title":"News for v5","date":"2018-03-25T00:19:00.000Z","path":"/news-for-v5/","parent":"/home/hypersprite/Documents/code/araceathlete-gatsby-material-ui-v1/src/pages/new-for-v5/index.md absPath of file","tags":"news","description":"News, Activity Search out of beta, updated to v5","cover":null}}},"pathContext":{"slug":"/new-for-v5/"}}

